.contect{
    position: relative;
    width: 100%;
    height: 100vh;
    min-height: 700px;
    background-color: #222;
    .c_inner{
      width: 1292px;
        min-width: 1292px;
        height: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .c_title{
            color: #DB4437;
            font-size: 40px;
            font-weight: bold;
            text-align: left;
            margin: 120px 0;
        }

        ul{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 24px;
            margin-bottom: 30px;
            li{
                svg{
                    font-size: 50px;
                    color: #fff;
                    &:hover{color: #333;}
                }
            }
        }
    }
}



@import url(https://fonts.googleapis.com/css?family=Khula:700);
.text_ani4 {
    font-family: 'Russo One', sans-serif;
    position: absolute;
    top: 50%; 
    left: 0;
    transform: translateY(-50%);
  text {
    text-transform: uppercase;
    animation: stroke 5s 1 alternate;
    stroke-width: 1;
    stroke: #fff;
    fill: transparent;
    font-size: 100px;
    font-weight: bold;
  }
  @keyframes stroke {
    0%   {
       stroke: #f7f7f7;
      stroke-dashoffset: 25%; stroke-dasharray: 0 50%; stroke-width: 2;
    }
    70%  { stroke: #f7f7f7; }
    80%  { stroke: #f7f7f7; stroke-width: 2;}
    100% {
       stroke: #f7f7f7; 
      stroke-dashoffset: -25%; stroke-dasharray: 50% 0; stroke-width: 2;
    }
  }
}