

.p_section{
    position: relative;
    display: flex;
    width: 100%;
    height: 100vh;
    background-color: #222;
    border-bottom: 1px solid #999;
    min-height: 700px;
    .videos{
        display: none;
        video{
            display: none;
        }
    }
    &:first-child{
        img{
            animation: ani2 10s linear 0s infinite alternate;
            }
            .m_img{
                animation: m_ani 10s linear 0s infinite alternate;
            }
            .t_img{
                animation: t_ani 10s linear 0s infinite alternate;
            }
    @keyframes m_ani {
        0%{top: 0;}
        100%{top: -1892px;}
    }
    @keyframes t_ani {
        0%{top: 0;}
        100%{top: -1319px;}
    }
    @keyframes ani2 {
        0%{top: 0;}
        100%{top: -2747px;}
    }
    }
    &:nth-child(2){
        img{
            animation: ani3 3s linear 0s infinite alternate;
            }
    .m_img{
        animation: m_ani3 3s linear 0s infinite alternate;
    }
    .t_img{
        animation: t_ani3 3s linear 0s infinite alternate;
    }
    @keyframes ani3 {
        0%{top: 0;}
        100%{top: -660px;}
    }
    @keyframes m_ani3 {
        0%{top: 0;}
        100%{top: -450px;}
    }
    @keyframes t_ani3 {
        0%{top: 0;}
        100%{top: -914px;}
    }
    }
    &:nth-child(3){
        .videos{
            display: inline-block;
            border: 10px solid #000;
            background-color: #fff;
            border-radius: 30px;
            overflow: hidden;
            width: 380px;    
            height: 480px;
            margin-left: 100px;
            video{
                display: block;
                width: 100%;
                height: 100%;
                object-fit: fill;
            }
        }
        .img_info,
        .moblie_img,
        .tablet_img{display: none;}
        img{
        display: none;
            }
    @keyframes ani4 {
        0%{top: 0;}
        100%{top: -430px;}
    }
    }
    &:nth-child(4){
            .img_info,
            .moblie_img,
            .tablet_img{display: none;}
            img{
            display: none;
                }
                .videos{
                    display: inline-block;
                    border: 10px solid #000;
                    background-color: #fff;
                    border-radius: 30px;
                    overflow: hidden;
                    width: 600px;    
                    height: 400px;
                    video{
                        display: block;
                        width: 100%;
                        height: 100%;
                        object-fit: fill;
                    }
                    
                }
    }
    .prev,.next{
        background: transparent;
        border: none;
        z-index: 1;
        font-size: 100px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 93%;
        cursor: pointer;
        color: #fff;
        &:hover{
            color: #000;
        }
    }
    .prev{
        left: 1%;
    }
    .color{
        &:hover{
            color: #999;
        }
    }
    .project_info{
        position: relative;
        width: 1292px;
        min-width: 1292px;
        margin: 0 auto;
        .p_title{
            font-size: 40px;
            font-weight: bold;
            text-align: left;
            color: #4285F4;
            margin: 120px 0;
        }
        .nums{
            position: absolute;
            top: 120px;
            right: 10%;
            font-size: 32px;
            color: #fff;
        }
    }
    .imgs{
        position: absolute;
        .img_info{
            float: left;
            border: 10px solid #000;
            background-color: #fff;
            position: relative;
            width: 600px;    
            height: 400px;
            overflow: hidden;
            border-radius: 30px;
            img{
                position: absolute;
                top: 100%;
                width: 100%;
            }
        }
        .tablet_img{
            border: 10px solid #000;
            background-color: #fff;
            position: absolute;
            bottom: 0;
            right: -120px;   
            width: 300px;
            height: 200px;
            overflow: hidden;
            border-radius: 30px;
            img{
                position: absolute;
                top: 100%;
                width: 100%;
            }
        }
        .moblie_img{
            border: 10px solid #000;
            background-color: #fff;
            position: absolute;
            bottom: 0;
            left: -50px;   
            width: 120px;
            height: 200px;
            overflow: hidden;
            border-radius: 30px;
            img{
                position: absolute;
                top: 100%;
                width: 100%;
            }
        }
    }

    .text_info{
        float: right;
        color: #fff;
        margin-right: 100px;
        height: 400px;
        .title{
            font-size: 24px;
            margin: 30px 0;
        }
        .subTitle{
            font-size: 20px;
            margin-bottom: 30px;
        }
        .text{
            margin-bottom: 30px;
        }
        .language{
            margin-bottom: 30px;
        }
        .url{
            margin-top: 30px;
            span{
                a{
                    width: 150px;
                    padding: 10px;
                    display: inline-block;
                    text-decoration: none;
                    color: #fff;
                    font-size: 16px;
                    border: 1px solid #222;
                    border-radius: 5px;
                    text-align: center;
                    margin-right: 3px;
                    background: #000;
                    transition: all 0.2s linear 0s;
                    &:hover{
                        color: #000;
                        background: #fff;
                    }
                }
            }
        }
        .list{
            color: #81c147;
            margin-right: 5px;
            vertical-align: middle;
        }
    }
}