.about{
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: #222;
    border-bottom: 3px solid #999;
    min-height: 700px;
    &::after{
        display: block;
        content: "";
        clear: both;
    }
    .about_title{
        color: #0F9D58;
    }
    .a_inner{
        width: 1292px;
        min-width: 1292px;
        margin: 0 auto;
        height: 100%;
        color: #fff;
        &::after{
            display: block;
            content: "";
            clear: both;
        }
        .a_title{
            font-size: 40px;
            padding-top: 120px;
            text-align: left;
            color: #F4B400;
            margin-bottom: 100px;
        }
            .me{
                margin-right: 50px;
                float: left;                
                img{
                    width: 400px;
                }
            }
            .about_me{
                width: 830px;
                flex-direction: column;
                float: right;
            .intro{
                font-size: 24px;
                margin-bottom: 30px;
            }
            p{
                margin-bottom: 30px;
            }
            .my_skills{
                h3{
                    font-size: 24px;
                    margin-bottom: 30px;
                }
                ul{
                    display: flex;
                    align-items: center;
                    margin-bottom: 30px;
                    .css{
                        height: 70px;
                        img{
                            height: 100%;
                        }
                    }
                    .sass{
                        width: 150px;
                    }
                    .firebase{
                        width: 150px;
                    }
                    li{
                        margin-right: 50px;
                        width: 70px;
                        img{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}