
html,body{
    height: 100%;
    *{
        padding: 0;
        margin: 0;
        box-sizing: border-box;
    }
    ul,ol,li{
        list-style: none;
    }
    .wrap{
        animation: ani 1s linear 0s 1;
        overflow: hidden;
        .left_deco,.right_deco{
            position: fixed;
            font-size: 100px;
            color: #c5c5c5;
        }
        .left_deco{
            top: 50px; 
            left: 50px;
        }
        .right_deco{
            top: 800px;
            right: 50px;
        }
        .projects{
            position: relative;
            display: flex;
            width: 400%;
            transition: all 0.25s ease-in 0s;
        }
    }
    
    @keyframes ani{
        0%{opacity: 0;}
        100%{opacity: 1;}
    }
}